.createPostContainer {
    z-index: 1000;

    display: flex;
    flex-direction: column;

    width: 800px;

    

    background: #FEFFFF;
    border-radius: 25px;
    box-shadow: rgba(0, 0px, 0px, 0.35) 0px 5px 15px;

    padding: 1em 2em;
}

.portalBackdrop {
    z-index: 999;

    backdrop-filter: brightness(60%);

    display: flex;
    align-items: center;
    justify-content: center;
    overflow-y: hidden;

    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: 100%;
}