.App {
    display : flex;
    flex-direction : column;
    justify-content : center;
    align-items : center;
  }

.header {
    width : 25%;
    display : flex;
    justify-content : center;
    flex-direction : column;
    align-items : center;

    color : #DEF2F1;
    background : #2B7A78;
    border-radius: 25px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.DisplayPosts {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)px;
}

.fade-in {
    animation: fadeIn 2s;
}

@keyframes fadeIn {
    from {
        opacity : 0;
    }
    to {
        opacity : 1;
    }
}